import React from "react";
import { Grid, Column } from "@carbon/react";
import { Globe, RainDrop, Windy } from "@carbon/react/icons";
import dayjs from "dayjs";

import CurrentConditions from "../../content/CurrentWeather/CurrentConditions";
import { aeris_coded_weather } from "../../utils/AerisUtils";
import { aeris_icon } from "../../utils/AerisUtils";

// Take in a phrase and separate the third word in an array
function createArrayFromPhrase(phrase) {
  const splitPhrase = phrase.split(" ");
  const thirdWord = splitPhrase.pop();
  return [splitPhrase.join(" "), thirdWord];
}
const TestCard = (props) => {
  const forecast = props.forecast;
  const daily = forecast.forecast_24hr.response[0];
  return (
    <div className="sb-css-grid-container">
      <div className="cds--css-grid">
        {daily.periods.map((day) => {
          // console.log(day);
          return (
            <div
              className="cds--sm:col-span-4 cds--css-grid-column"
              key={day.timestamp}
            >
              <h5>{dayjs.unix(day.timestamp).format("ddd MM/DD")}</h5>
              <div className="forecast-conditions">
                <CurrentConditions name={aeris_icon(day.icon)} />
                <p>{aeris_coded_weather(day.weatherPrimaryCoded, false)}</p>
              </div>
              <div className="forecast-data">
                <span className="forecast-high">
                  {day.maxTempF}
                  &#176;
                </span>{" "}
                |{" "}
                <span className="forecast-low">
                  {day.minTempF}
                  &#176;
                </span>
                <div className="forecast-precip">
                  <span>
                    <RainDrop size={16} />
                    &nbsp;
                    {day.pop}
                    {"%"}
                  </span>
                </div>
                <span>
                  <Windy />
                  &nbsp;
                  {day.windSpeedMPH} | {day.windGustMPH}
                </span>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

const InfoSection = (props) => (
  <Grid className={`${props.className} info-section`}>
    <Column md={8} lg={4} xlg={3}>
      <h3 className="info-section__heading">{props.heading}</h3>
    </Column>
    {props.children}
  </Grid>
);
const InfoCard = (props) => {
  const splitHeading = createArrayFromPhrase(props.heading);
  var forecast = props.forecast;

  return (
    <Column sm={4} md={8} lg={4} className="info-card">
      <h3 className="info-card__heading">{forecast}</h3>

      <h4 className="info-card__heading">
        {`${splitHeading[0]} `}
        <strong>{splitHeading[1]}</strong>
      </h4>
      <p className="info-card__body">{props.body}</p>
      {props.icon()}
    </Column>
  );
};

export { InfoSection, InfoCard, TestCard };
