import React from "react";

import MostlyClearDay from "./images/mostly-clear-day.svg";
import MostlyClearNight from "./images/mostly-clear-night.svg";
import PartlyCloudy from "./images/partly-cloudy-day.svg";
import PartlyCloudyNight from "./images/partly-cloudy-night.svg";
import Sunny from "./images/clear-day.svg";
import ClearNight from "./images/clear-night.svg";
import Cloudy from "./images/cloudy-day.svg";
import MostlyCloudy from "./images/mostly-cloudy-day.svg";
import MostlyCloudyShowers from "./images/mostly-cloudy-showers.svg";
import Showers from "./images/showers.svg";
import BlowingDust from "./images/blowing-dust.svg";
import Hazy from "./images/hazy.svg";
import Fog from "./images/fog.svg";
import Rain from "./images/rain.svg";
import Thunderstorm from "./images/thunderstorm.svg";

function CurrentConditions(props) {
  let icon = props.name;
  let conditionsDay = icon.replace(/\.[^/.]+$/, "");
  switch (conditionsDay) {
    case "mostly-clear-day":
      return <img src={MostlyClearDay} width="140px" alt="weather icon" />;
    case "mostly-clear-night":
      return <img src={MostlyClearNight} width="140px" alt="weather icon" />;

    case "partly-cloudy-day":
      return <img src={PartlyCloudy} width="140px" alt="weather icon" />;
    case "partly-cloudy-night":
      return <img src={PartlyCloudyNight} width="140px" alt="weather icon" />;
    case "clear-day":
      return <img src={Sunny} width="140px" alt="weather icon" />;
    case "fair":
      return <img src={Sunny} width="140px" alt="weather icon" />;
    case "sunny":
      return <img src={Sunny} width="140px" alt="weather icon" />;
    case "clear-night":
      return <img src={ClearNight} width="140px" alt="weather icon" />;
    case "mostly-cloudy-day":
      return <img src={MostlyCloudy} width="140px" alt="weather icon" />;
    case "mostly-cloudy-showers":
      return <img src={MostlyCloudyShowers} width="140px" alt="weather icon" />;
    case "showers":
      return <img src={Showers} width="140px" alt="weather icon" />;
    case "blowing-dust":
      return <img src={BlowingDust} width="140px" alt="weather icon" />;
    case "hazy":
      return <img src={Hazy} width="140px" alt="weather icon" />;
    case "smoke":
      return <img src={Hazy} width="140px" alt="weather icon" />;
    case "fog":
      return <img src={Fog} width="140px" alt="weather icon" />;
    case "rain":
      return <img src={Rain} width="160px" alt="weather icon" />;
    case "cloudy-day":
      return <img src={Cloudy} width="140px" alt="weather icon" />;
    case "cloudy":
      return <img src={Cloudy} width="140px" alt="weather icon" />;
    case "thunderstorm":
      return <img src={Thunderstorm} width="160px" alt="weather icon" />;
    default:
      return <img src={PartlyCloudy} width="200px" alt="weather icon" />;
  }
}

export default CurrentConditions;
