import React, { useState, useEffect, Fragment } from "react";

import RenderOutTemp from "./RenderOutTemp";
import CurrentConditions from "./CurrentConditions";
import RenderPurpleAir from "./RenderPurpleAir";
// import { rotateThis } from './helpers';

const CurrentWeather = () => {
  const weatherData_URL =
    "https://s3-us-west-2.amazonaws.com/astroguitar.com/reh/json/weewx_data.json";

  const forecastData_URL =
    "https://s3-us-west-2.amazonaws.com/astroguitar.com/reh/json/forecast.json";

  const [weather, setWeather] = useState([]);
  const [current, setCurrent] = useState([]);
  const [forecast, setForecast] = useState([]);
  // const [darksky, setDarksky] = useState([]);
  // const [purpleAir, setPurpleAir] = useState([]);

  const [minutely, setMinutely] = useState([]);
  // Sets default React state

  const [connectionStatus, setConnectionStatus] = useState(false);
  const getCurrent = async () => {
    const response = await fetch(weatherData_URL);
    const weather = await response.json();
    const forecastResponse = await fetch(forecastData_URL);
    const forecast = await forecastResponse.json();

    // const response = await fetch(purpleAirData_URL, {
    //   headers: {
    //     "X-API-Key": "DD8E9A61-E02D-11EC-8561-42010A800005",
    //   },
    // });

    // const aqiData = await response.json();
    setForecast(forecast);
    setWeather(weather);
    setCurrent(weather.current);
    // setDarksky(forecast.darksky);
    console.log("forecast and weather data received...");
  };

  useEffect(() => {
    getCurrent();

    const interval = setInterval(() => {
      getCurrent();
    }, 600000);

    return () => clearInterval(interval);
  }, []); /* Must make sure to run dependency array [], otherwise infinite loop ...  */

  if (weather.length === 0) return null;
  if (forecast.length === 0) {
    return null;
  }

  const dayjs = require("dayjs");
  var aqi_location = [];

  var stat_6hr = 0.0;
  var stat_24hr = 0.0;
  var stat_30min = 0.0;
  var stat_60min = 0.0;
  var statavg = 0.0;

  var pm25 = 0.0;

  if (forecast.purpleair) {
    aqi_location = forecast.purpleair.sensor.name;
    stat_6hr = forecast.purpleair.sensor.stats["pm2.5_6hour"];
    stat_24hr = forecast.purpleair.sensor.stats["pm2.5_24hour"];
    stat_30min = forecast.purpleair.sensor.stats["pm2.5_30minute"];
    stat_60min = forecast.purpleair.sensor.stats["pm2.5_60minute"];
    statavg = (stat_60min + stat_30min) / 2.0;
    // console.log("statavg = " + statavg);
    if (statavg) {
      // pm25 = parseFloat(statavg).toFixed(1);
    }
    pm25 = parseFloat(statavg).toFixed(1);
    // console.log("purpleair PM2.5 value = ", pm25)
  }
  var newRotation = 0;
  var newWindDir = 0;
  newWindDir = current.winddir_formatted;

  // newRotation = rotateThis(newWindDir);

  // const style = {
  //   transform: "rotate(" + newRotation + ")",
  //   transition: "transform 150ms ease", // smooth transition
  // };

  var cloudCover;
  var currentWeather;
  currentWeather = forecast.metar && forecast.metar.response.ob.weather;
  cloudCover = forecast.metar && forecast.metar.response.ob.sky;
  var renderVisibility = [];
  var visibility = [];
  visibility = forecast.metar && forecast.metar.response.ob.visibilityMI;
  if (!visibility) visibility = "NA";
  renderVisibility = function () {
    return (
      <td>
        <span className="visibility">{Math.round(visibility)} miles</span>
      </td>
    );
  };

  // Metar
  var ceiling = "unknown";
  if (forecast.metar.response.ob.ceilingFT === null) {
    ceiling = "none reported";
  } else {
    ceiling = forecast.metar.response.ob.ceilingFT + " ft";
  }
  var renderVisibility = [];
  var visibility = [];
  visibility = forecast.metar && forecast.metar.response.ob.visibilityMI;
  if (!visibility) {
    visibility = "NA";
  } else {
    visibility = Math.round(visibility) + " mi";
  }
  renderVisibility = function () {
    return (
      <td>
        <span className="visibility">{Math.round(visibility)} miles</span>
      </td>
    );
  };

  return (
    <>
      <div className="cds--data-table-header">
        <h4 className="cds--data-table-header__title" id="tc-:r1h:-title">
          current conditions
        </h4>
        <p
          className="cds--data-table-header__description"
          id="tc-:r1h:-description"
        >
          Last Updated: {current.datetime}
        </p>
      </div>

      <div className="cds--css-grid">
        <div className="cds--sm:col-span-4 cds--md:col-span-5 cds--lg:col-span-6 cds--css-grid-column">
          <div
            role="grid"
            className="cds--structured-list"
            aria-label="Structured list"
          >
            <div className="cds--structured-list-tbody" role="rowgroup">
              <div tabIndex="0" role="row" className="cds--structured-list-row">
                <div className="cds--structured-list-td" role="cell">
                  {/* Row 1 Column 1 */}
                  <CurrentConditions
                    name={forecast.metar && forecast.metar.response.ob.icon}
                  />
                  <h3>{currentWeather}</h3>
                  <p className="curwindspeed">
                    Wind: {current.windspeed} mph {current.windcompass}
                  </p>
                  <p className="curwindgust">
                    Gust: {current.windGust_formatted} mph
                  </p>
                  <p>
                    Cloud Cover:{" "}
                    {forecast.metar && forecast.metar.response.ob.sky}%
                  </p>
                </div>
                <div
                  className="outtemp-cell cds--structured-list-td"
                  role="cell"
                >
                  {/* Row 1 Column 2 */}
                  <RenderOutTemp
                    temp={current.outTemp_formatted}
                    appTemp={current.appTemp}
                  />
                  <div
                    className="table-container"
                    role="table"
                    aria-label="High and Low"
                  >
                    <div className="flex-table rowspan" role="rowgroup">
                      <div className="column">
                        <div className="flex-row">
                          <div className="flex-cell" role="cell">
                            High
                          </div>
                          <div className="flex-cell" role="cell">
                            {weather.day && weather.day.outTemp.max} &#176;F
                          </div>
                        </div>

                        <div className="flex-row">
                          <div className="flex-cell" role="cell">
                            Low
                          </div>
                          <div className="flex-cell" role="cell">
                            {weather.day && weather.day.outTemp.min} &#176;F
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="rain-data">
                    <ul>Rain:&nbsp;{current.rain}</ul>
                    <ul>Rate:&nbsp;{current.rainRate}</ul>
                    <ul>Day:&nbsp;&nbsp;{current.rainDay}</ul>
                  </div>
                </div>
              </div>
              <div tabIndex="0" role="row" className="cds--structured-list-row">
                <div className="cds--structured-list-td" role="cell">
                  <div>
                    <RenderPurpleAir val={pm25} location={aqi_location} />
                  </div>
                </div>
                <div className="cds--structured-list-td" role="cell" />
              </div>
            </div>
          </div>
        </div>
        <div className="cds--sm:col-span-4 cds--md:col-span-3 cds--lg:col-span-5 cds--css-grid-column">
          <div className="cds--data-table-container">
            <div className="cds--data-table-header">
              <h4 className="cds--data-table-header__title" id="tc-:r1h:-title">
                latest observations
              </h4>
              <p
                className="cds--data-table-header__description"
                id="tc-:r1h:-description"
              />
            </div>
            <div className="cds--data-table-content">
              <table
                aria-labelledby="tc-:r1h:-title"
                className="cds--data-table cds--data-table--xs"
              >
                <tbody aria-live="polite">
                  <tr>
                    <td>Barometer</td>
                    <td>{current.barometer}</td>
                  </tr>
                  <tr>
                    <td>Visibility</td>
                    <td>{visibility}</td>
                  </tr>
                  <tr>
                    <td>Ceiling</td>
                    <td>{ceiling}</td>
                  </tr>
                  <tr>
                    <td>Dewpoint</td>
                    <td>{current.dewpoint} &#176;F</td>
                  </tr>
                  <tr>
                    <td>Humidity</td>
                    <td>{current.outHumidity}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <div className="cds--sm:col-span-4 cds--md:col-span-6 cds--lg:col-span-6 cds--css-grid-column">
          <h2> MAP</h2>
        </div>
      </div>
    </>
  );
};

export default CurrentWeather;
